import React from "react";
import payment from "../../img/payment.jpg";

const Home = props => {
  return (
    <div className="Home">
      <h3>Welcome to use this todo app!</h3>
      <p>Start using from right now!</p>
      <p>
        Data stored on browser. If you close browser, you will <em>[lose]</em>{" "}
        all data!
      </p>
      <p>
        数据未存入服务器，关闭浏览器将 <em>[失去]</em> 所有数据!
      </p>
      <img src={payment} />
    </div>
  );
};
export default Home;
